import Vue from 'vue'
import axios from 'axios';
import router from '../router'

Vue.prototype.axios = axios;
axios.defaults.timeout = 10000; //
import { Message } from 'element-ui';
// axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
if (location.hostname == "super.zhengyi100.cn") {
    axios.defaults.baseURL = 'https://admin.api.zhengyi100.cn'; //,
} else {
    axios.defaults.baseURL = 'http://dev.admin.api.zhengyi100.cn'; //

}


// 
axios.interceptors.request.use(function(config) {
    let token = localStorage.getItem('token');
    if (token) {
        config.headers.Authorization = token;
    }
    return config;
}, function(error) {
    // 
    Message.error('');
    return Promise.reject(error);
});

// 
axios.interceptors.response.use(function(response) {
    let token = response.config.headers.Authorization;
    if (token) {
        localStorage.setItem('token', token)
    }
    // 
    return response.data;
}, function(error) {
    if (error.message.includes('timeout')) {
        Message.error('');
    } else {
        Message.error('');
    }
    // 
    return Promise.reject(error);
});


//post
export function fetchPost(url, params, config) {
    return new Promise((resolve, reject) => {
        axios.post(url, params, config)
            .then(res => {
                if (res.code == 114 || localStorage.getItem('token') == '') {
                    Message.error('')
                    router.push("/login")
                    localStorage.clear('token');
                    localStorage.clear('userinfo');
                } else {
                    resolve(res)
                }
            })
            .catch(error => {
                reject(error)
            })
    })
}

//get
export function fetchGet(url, param) {
    return new Promise((resolve, reject) => {
        axios.get(url, { params: param })
            .then(res => {
                resolve(res.data)
            })
            .catch(error => {
                reject(error)
            })
    })
}