const router = new Router({
        routes: [{
                path: '/',
                redirect: '/login'
            },
            {
                path: '/login',
                name: '',
                component: login
            },
            {
                path: '/home',
                name: 'home',
                component: home,
                redirect: '/dataStatistics',
                children: [
                    ...asyRouter
                ]
            },
            {
                path: '*',
                component: noFound
            }
        ]
    })
    // 
router.beforeEach((to, from, next) => {
    let isLogin = localStorage.getItem('token') // 
    if (to.meta.needLogin) { // 
        if (isLogin) { // 
            next()
        } else { //
            next({
                path: '/login',
                query: { redirect: to.fullPath } // path
            })
        }
    } else {
        next()
    }
})

export default router