function vestStatus(status) {
    switch (status) {
        case 0:
            return '';
            break;
        case 1:
            return '()';
            break;
        case 2:
            return '(|)';
            break;
        case 3:
            return '(|)';
            break;
    }
}

//
function copyArray(arr, result) {
    var arr = arr || [];
    var result = result || [];
    for (var i = 0; i < arr.length; i++) {
        if (arr[i] instanceof Array) {
            result[i] = [];
            copyArray(arr[i], result[i]);
        } else {
            result[i] = arr[i];
        }
    }
    return result;
}



/**
 * 
 * @param {number} page 
 * @param {number} limit 
 * @param {number} tot 
 */
function pagination(page, limit, tot) {
    console.log(page)
    if (tot % limit === 1 && page != 1) {
        let p = parseInt(tot / limit) + 1;
        if (page >= p) {
            return page - 1;
        } else {
            return page;
        }

    } else {
        return page;
    }
}

